* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-weight: 500;
}

body {
  background-color: #f6f0dd;
  height: 100%;
}

.react-icons {
  margin: 0.5em;

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: all 0.3s;
  }
}

@media only screen and (max-width: 480px) {
  .extra {
    display: none;
  }
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
}

.wrapper {
  position: relative;
  text-align: center;
  height: 100vh;
}

.name {
  font-weight: 600;
}
